.login{
  display: grid;
  place-items: center;
  height: 100vh;
}
.login__logo > img{
  object-fit: contain;
  height: 150px;
  background-color: transparent;
}
.login__logo{
  display: flex;
  flex-direction: column;
}
.login > button{
  width: 300px;
  background-color: #2e81f4;
  color:#eff2f5;
  font-weight: 800;
}
.login > button:hover{
  background-color: #fff;
  color:#2e81f4;
  font-weight: 800;
}
