.header{
  display:flex;
  justify-content: space-between;
  padding:20px 20px;
  position: sticky;
  background-color: white;
  z-index:100;
  top:0;
  box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
}
.header__left{
  display: flex;
}
.header__left > img{
  width: 40px;
  align-self: flex-start;
}
.header__input{
  display:flex;
  align-items: center;
  background-color:#eff2f5;
  padding: 10px;
  margin-left:10px;
  border-radius: 1000px;
}
.header__input > input{
  border: none;
  background-color: transparent;
  outline: none;
}
.header__center{
  display: flex;
}
.header__option > .MuiSvgIcon-root{
  color: gray;
}

.header__option:hover > .MuiSvgIcon-root{
  color: #2e81f4;
}
.header__option--active > .MuiSvgIcon-root{
  color: #2e81f4;
}
.header__option--active{
  border-bottom:  4px solid #2e81f4;
}
.header__option{
  padding: 0 30px;
  cursor: pointer;
  display:flex;
  align-items: center;
}
.header__option:hover{
  background-color: #eff2f5;
  border-radius: 10px;
  align-items: center;
  padding: 0 30px;
  border-bottom: none;
}
.header__info{
  display: flex;
  align-items: center;
}
.header__info > h4{
  margin-left: 10px;
}
.header__right{
  display: flex;
}
