.messageSender{
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
  width: 100%;
}
.messageSender__top{
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #eff2f5;
}
.messageSender__top > form{
  flex: 1;
  display: flex;
}
.messageSender__top > form > input{
  outline: none;
  border: none;
  padding: 10px 20px;
  margin: 0 10px;
  border-radius:  999px;
  background-color: #eff2f5;
}
.messageSender__top > form > button{
  display: none;
}
.messageSender__input{
  flex: 1;
}
.messageSender__bottom{
  display: flex;
  justify-content: space-evenly;
}
.messageSender__option{
  padding: 20px;
  display: flex;
  align-items: center;
  color: gray;
  margin: 5px;
}
.messageSender__option > h3{
  margin-left: 10px;
  font-size: medium;
  cursor: pointer;
}
.messageSender__option:hover{
  background-color: #eff2f5;
  border-radius: 20px;
}
